import React from 'react';

export default class Card extends React.Component {
    state = {
        edit: false,
        newText: ''
    }
    remove = (id) => {
        this.props.remove(id)
    }
    save = (id) => {
        this.state.newText === '' ? this.props.saveNewText(this.props.todo.text, id) : this.props.saveNewText(this.state.newText, id);
        this.setState({
            edit: !true
        })
    }
    newText = (e) => {
        this.setState({
            newText: e.target.value
        })
    }
    edit = () => {
        this.setState({
            edit: !false
        })
    }
    render() {
        if(this.state.edit) {
            return (
                <div className="card blue-grey darken-1">
                <div className="card-content white-text">
                  <input id="email" type="text" onChange={this.newText} defaultValue={this.props.todo.text}/>
                </div>
                <div className="card-action">
                  <a href="#" onClick={() => this.save(this.props.id)}>Сохранить</a>
                  <a href="#" onClick={() => this.remove(this.props.id)}>Удалить</a>
                </div>
              </div>
            )
        }
        else {
            return (
                <div className="card blue-grey darken-1">
                <div className="card-content white-text">
                  <span className="card-title">{this.props.todo.text}</span>
                </div>
                <div className="card-action">
                  <a href="#" onClick={this.edit}>Редактировать</a>
                  <a href="#" onClick={() => this.remove(this.props.id)}>Удалить</a>
                </div>
              </div>
            )
        }
    }
}



