import React from 'react';
import ReactDOM from 'react-dom';
import ReactCSSTransitionGroup  from 'react-addons-css-transition-group';
import './index.css';
import 'materialize-css/dist/css/materialize.min.css';

import Input from './templ/Input';
import Card from './templ/Card';

class App extends React.Component {
    state = {
        tasks: [],
        first: [],
        second: [],
        third: []
    }
    submit = (text, portal) => {
        let mass = this.state.tasks;
        mass.push({id: ++ mass.length, text: text, portal: portal});
        let first = mass.filter((todo, item) => todo.portal === 'first');
        let second = mass.filter((todo, item) => todo.portal === 'second');
        let third = mass.filter((todo, item) => todo.portal === 'third');
        this.setState({
            first: first
        })
        this.setState({
            second: second
        })
        this.setState({
            third: third
        })

    }
    remove = (id) => {
        this.setState({
            tasks: this.state.tasks.filter((todo, item) => todo.id !== id)
        })
        this.setState({
            first: this.state.first.filter((todo, item) => todo.id !== id)
        })
        this.setState({
            second: this.state.second.filter((todo, item) => todo.id !== id)
        })
        this.setState({
            third: this.state.third.filter((todo, item) => todo.id !== id)
        })
    }
    saveNewText = (text, id) => {
        let arr = this.state.tasks;
        let filt = arr.filter((todo, item) => todo.id === id);
        filt[0].text = text;
        this.setState({
            tasks: arr
        })
    }
    check = (item) => {
        this.setState({
            category: item
        })
    }
    render() {
        return (
            <div className="wrapper">
                    <Input submit={this.submit} />

                {ReactDOM.createPortal(
                    <ReactCSSTransitionGroup transitionName="example" transitionEnter={true} transitionLeave={true} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                    {this.state.first.map((todo, id) => {
                        return <Card todo={todo} id={todo.id} key={todo.id} remove={this.remove} saveNewText={this.saveNewText} />
                    })}</ReactCSSTransitionGroup>,
                    document.getElementById('first'),
                )}

                {ReactDOM.createPortal(
                    <ReactCSSTransitionGroup transitionName="example" transitionEnter={true} transitionLeave={true} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                    {this.state.second.map((todo, id) => {
                        return <Card todo={todo} id={todo.id} key={todo.id} remove={this.remove} saveNewText={this.saveNewText} />
                    })}</ReactCSSTransitionGroup>,
                    document.getElementById('second'),
                )}

                {ReactDOM.createPortal(
                    <ReactCSSTransitionGroup transitionName="example" transitionEnter={true} transitionLeave={true} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                    {this.state.third.map((todo, id) => {
                        return <Card todo={todo} id={todo.id} key={todo.id} remove={this.remove} saveNewText={this.saveNewText} />
                    })}</ReactCSSTransitionGroup>,
                    document.getElementById('third'),
                )}

            </div>
        )
    }
};

ReactDOM.render(<App />, document.getElementById('root'));



