import React from 'react';

export default class Input extends React.Component {
    state = {
        value: '',
        check: 'first'
    }
    tiping = (e) => {
        this.setState({
            value: e.target.value
        })
    }
    submit = (text) => {
        if(text !== '') {
            this.props.submit(text, this.state.check);
            this.setState({
                value: ''
            });
        }
    }
    check = (item) => {
        this.setState({
            check: item
        })
    }
    render() {
        return (
            <div className="wraps">
            <div className="inp-wrap">
            <input id="password" type="text" value={this.state.value} onChange={this.tiping} className="validate" />
            <a className="waves-effect waves-light btn" onClick={() => this.submit(this.state.value)}>Создать</a>
            </div>
            <div className="check-wrap">
            <p>
                <input name="group1" type="radio" defaultChecked='true' onChange={() => this.check('first')} id="test1" />
                <label htmlFor="test1">Краткосрочные</label>
            </p>
            <p>
                <input name="group1" type="radio" onChange={() => this.check('second')} id="test2" />
                <label htmlFor="test2">Среднесрочные</label>
            </p>
            <p>
                <input name="group1" type="radio" onChange={() => this.check('third')} id="test3" />
                <label htmlFor="test3">Долгосрочные</label>
            </p>
            </div>
            </div>
        )
    }
}



